import { Link } from "react-router-dom";
import React from "react";
import { blog_data } from "../../../data";

const blog_items = blog_data.filter((blog) => blog.kindergarten_blog);

const BlogArea = () => {
  return (
    <div className="edu-blog-area blog-area-4 edu-section-gap">
      <div className="container">
        <div
          className="section-title section-center"
          data-sal-delay="150"
          data-sal="slide-up"
          data-sal-duration="800"
        >
          <span className="pre-title pre-textsecondary">Our Articles</span>
          <h2 className="title">Check Out Our Latest Blog</h2>
          <span className="shape-line">
            <i className="icon-19"></i>
          </span>
        </div>

        <div className="row g-5">
          {blog_items.map((blog) => {
            const { id, img, title, category, comment, date, sm_desc } = blog;
            return (
              <div
                key={id}
                className="col-lg-4 col-md-6 col-12"
                data-sal-delay="150"
                data-sal="slide-up"
                data-sal-duration="800"
              >
                <div className="edu-blog blog-style-3">
                  <div className="inner">
                    <div className="thumbnail">
                      <Link to={`/blog-details/${id}`}>
                        <a>
                          <img src={img} alt="Blog Images" />
                        </a>
                      </Link>
                      <div className="read-more-btn">
                        <Link to={`/blog-details/${id}`}>
                          <a className="btn-icon-round">
                            <i className="icon-4"></i>
                          </a>
                        </Link>
                      </div>
                    </div>

                    <div className="content">
                      <div className="category-wrap">
                        <Link to={`/blog-details/${id}`}>
                          <a className="blog-category">{category}</a>
                        </Link>
                      </div>
                      <h5 className="title">
                        <Link to={`/blog-details/${id}`}>
                          <a>{title}</a>
                        </Link>
                      </h5>
                      <ul className="blog-meta">
                        <li>
                          <i className="icon-27"></i>
                          {date}
                        </li>
                        <li>
                          <i className="icon-28"></i>Com {comment}
                        </li>
                      </ul>
                      <p>{sm_desc}</p>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default BlogArea;
